</el-row>
  </el-header>
    <el-main>
        <Scan style="width:1200px;margin: auto; left:0; top:0; right:0; bottom:0; border-left: solid 0px #e6e6e6"></Scan>
    <router-view></router-view>
    </el-main>
    </el-container>
    <el-dialog
  :visible.sync="dialogVisible"
  width="0%"
  top="40vh"
  center>
  <div class="login_box">
            <div class="avator_box">
                <img id="img" src="./assets/logo.png" alt="">
            </div>
            <el-form ref="loginFormRef" :rules="rules" :model="loginForm" class="login_form" label-width="0px">
            <el-form-item prop="username">
            <el-input v-model="loginForm.username" prefix-icon="el-icon-user" clearable></el-input>
            </el-form-item>
            <el-form-item prop="password">
            <el-input  v-model="loginForm.password"  prefix-icon="el-icon-lock" show-password></el-input>
            </el-form-item>
             <el-form-item class="btns">
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="info" @click="resetForm">重置</el-button>
            </el-form-item>
            </el-form>
        </div>    
</el-dialog>
    </div>
  </template>
  
<script>
import Scan from './views/Scan.vue'
export default {
    components: {Scan},
    data() {
        return {
            clientHeight:'',
            dialogVisible: false,
            loginForm : {
                username:"",
                password:""
        },
        rules :{
            username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
            password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        }
        }
    },
    methods: {
        
        resetForm(){
            this.$refs.loginFormRef.resetFields();
        },
        isdiaRun(){
            if (window.sessionStorage.getItem('token')){
                this.$router.push("/runcase");
                return this.dialogVisible = false;
            }else{
                return this.dialogVisible = true;
            }
        },
        login(){
            this.$refs.loginFormRef.validate(vail => {
                if(!vail) return;
                this.$axios.post("/login",this.loginForm).
                    then(res=>{
                        console.log(res);
                        if(res.data.status == 0){
                            this.dialogVisible = false;
                            window.sessionStorage.setItem('token',res.data.token);
                            window.sessionStorage.setItem('username',res.data.username);
                            this.$router.push("/runcase")
                            return this.$notify({title: '成功',message: '登录成功',type: 'success'})
                        }else if(res.data.status == 2){
                            return this.$notify({title: '失败',message: '密码错误',type: 'error'})
                        }else if(res.data.status == 1){
                            return this.$notify({title: '失败',message: '用户不存在',type: 'error'})
                        }
                        })
            })
        }
    }
    
}
</script>
  
